<template>
    <div id="user">
        <Bar title="所有用户"></Bar>
        <el-table :data="pageData" border style="width: 100%">
          <el-table-column label="ID" width="100">
              <template scope="scope">
                  <span>{{ scope.row.id }}</span>
              </template>
          </el-table-column>
          <el-table-column label="用户名" width="200">
              <template scope="scope">
                  <span>{{ scope.row.user }}</span>
              </template>
          </el-table-column>
          <el-table-column label="昵称" width="200">
              <template scope="scope">
                  <span>{{ scope.row.nickname }}</span>
              </template>
          </el-table-column>
          <el-table-column label="个性签名">
              <template scope="scope">
                  <span>{{ scope.row.signature }}</span>
              </template>
          </el-table-column>
          <el-table-column label="操作" width="100">
              <template scope="scope">
                  <el-button size="small" type="danger" @click="del(scope.$index, scope.row)">删除</el-button>
              </template>
          </el-table-column>
        </el-table>
        <el-pagination
          layout="total, prev, pager, next"
          @current-change="getUser"
          :page-size="100"
          :total="userCount">
        </el-pagination>
    </div>
</template>

<script>
import Bar from './Bar.vue'

export default {
    name: 'User',
    data () {
        return {
            pageData: [],
            allUser: {},
        }
    },
    computed: {
        userCount: function () { return this.$store.counts.user; }
    },
    components: { Bar },
    mounted () {
        this.getUser(1);
    },
    methods: {
        getUser (pageId) {
            if (this.allUser[pageId]) {
                this.pageData = this.allUser[pageId];
                return;
            }
            this.$axios.get('http://127.0.0.1:3000/admin/getuser/' + (pageId - 1))
                .then((res) => {
                    this.pageData = res.data.result;
                    this.allUser[pageId] = res.data.result;
                })
                .catch((res) => { console.log(res); });
        },
        del (index, row) {
            this.$confirm('是否确定要删除该用户', '警告', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$axios.get('http://127.0.0.1:3000/admin/deluser/' + row.id)
                    .then(({data}) => {
                        if (data.result) {
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            this.pageData.splice(index, 1);
                            this.$store.updateCount('user');
                        }
                    }).catch((res) => { console.log(res); });
            }).catch(() => {});
        },
    },
}
</script>

<style>

</style>




